html,
body,
#root {
  height: 100%;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.calculator {
  border: 1px solid #ebebeb;
  box-shadow: 0 0 0 0.2rem rgba(235, 235, 235, 0.3);
  border-radius: 8px;
  padding: 10px;
  width: 320px;
}

.title {
  margin-bottom: 5px;
  text-align: center;
}

.row {
  margin: 0;
}

.col {
  padding: 5px;
}

.btn-warning,
.btn-warning:focus,
.btn-warning:hover,
.btn-warning:not(:disabled):not(.disabled):active {
  color: white;
}

.col-form-control {
  padding-bottom: 15px;
}

.form-text {
  position: absolute;
  margin-top: 2px;
  font-size: 70%;
}

.footer {
  position: absolute;
  bottom: 5px;

  width: 190px;
  display: flex;
  justify-content: space-between;
}
